.tb_wt_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
}

.tb_wt_post_wrapper{
    padding: 7.5px 0;
    width: 100%;
}

.tb_wt_post_wrapper:first-child{
    padding-top: 0px;
}
.tb_wt_post_wrapper:last-child{
    padding-bottom: 0px;
}

.tb_wt_post_in{
    display: block;
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.05);
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}
.tb_wt_post_media_wrapp{
    position: relative;
}

.tb_wt_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}
.tb_wt_image, .tb_wt_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_wt_video{
    background-color: #000;
}

.tb_wt_video video,
.tb_wt_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_wt_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_wt_contant_wrapper {
    padding: 10px;
}
.tb_wt_share_container{
    z-index: 4;
}

.tb_wt_author_wrapper {
    padding: 10px;
}

.tb_wt_author {
    width: calc(100% - 30px);
}

.tb_wt_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
}
.tb_wt_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_wt_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    text-align: left;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_wt_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_wt_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_wt_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_wt_post_info {
    display: flex;
    align-items: center;
}

.tb_wt_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_wt_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_wt_username,
.tb_wt_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_wt_seprator {
    background-color: var(--tb-author-color);
}

.tb_wt_content,
.tb_wt_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_wt_social_action_counts__,
.tb_wt_social_action_ico__ {
    color: var(--tb-author-color);
}

.tb_wt_username, .tb_wt_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_wt_username{
    max-width: 65%;
}

.tb_wt_time{
    max-width: 35%;
}

.tb_wt_social_ {
    font-size: 24px;
}

.tb_wt_social_ico{
    color: var(--tb-icon-color);
}

.tb_wt_content {
    margin-bottom: 15px;
    white-space: pre-line;
    word-break: break-word;
}
.tb_wt_rating_content{
    margin-bottom: 15px;
}
.tb_wt_rating_content, .tb_wt_text_post {
    text-align: center;
}

.tb_wt_text_decoration::before{
    content: "\e909";
    font-family: 'WebSocialFonts';
    margin-right: 12px;
}

.tb_wt_text_decoration::after{
    content: "\e90a";
    font-family: 'WebSocialFonts';
    margin-left: 12px;
}

.tb_wt_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}

.tb_wt_share_wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.tb_wt_share_list {
    padding-left: 15px;
}

.tb_wt_share_icon {
    font-size: 18px;
    display: flex;
    transition: all 0.3s ease-in-out;
}

.tb_wt_share_icon:hover {
    transform: scale(1.15);
}
.tb_wt_video_ico, .tb_wt_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 1;
    color: #fff;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}
.tb_wt_video_ico{
    font-size: 12px;
}
.tb_wt_audio_ico{
    font-size: 16px;
}
.tb_wt_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_wt_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_wt_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
}

.tb_wt_post_share_container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.tb_wt_social_action__ {
    display: flex;
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
}

.tb_wt_social_action__list {
    display: flex;
    padding: 3px;
    margin: 3px;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}

.tb_wt_social_action_counts__ {
    font-size: 12px;
    margin-left: 5px;
    font-weight: 400;
}

.tb_wt_social_action__ico_wrap{
    display: flex;
    align-items: center;
    text-decoration: none !important;
}
.tb_wt_social_action_ico__ {
    font-size: 16px;
}

.tb_wt_post_cta {
    display: block;
    text-align: center;
    margin-bottom: 10px;
}

.tb_wt_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_wt_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}



.tb_wt_cursor_pointer{
    cursor: pointer;
}